@font-face {
  font-family: "hank";
  src: url(../../../assets/fonts/hankengrotesk-variablefont_wght-webfont.woff), url(../../../assets/fonts/hankengrotesk-variablefont_wght-webfont.woff2);
  font-weight: normal;
  font-style: normal;
}
html {
  font-family: "hank";
  font-size: 18px;
}

.hero-sec {
  padding: 2rem 3rem;
}
.hero-sec h1 {
  font-size: 0.9rem;
  margin-bottom: 1rem;
}
.hero-sec h2 {
  font-size: 2.3rem;
  font-weight: 800;
  margin-bottom: 1.3rem;
}
.hero-sec h2 span {
  font-size: 0.7rem;
  font-weight: 500;
  display: block;
}
.hero-sec h3 {
  margin-bottom: 0.8rem;
}
.hero-sec p {
  font-size: 0.8rem;
}

.summary-sec {
  padding: 1.4rem;
}
.summary-sec h3 {
  text-align: left;
  font-size: 1rem;
}
.summary-sec article {
  display: flex;
  justify-content: space-between;
  padding: 0.6rem;
  border-radius: 0.4rem;
  margin-bottom: 1rem;
  margin-top: 0.3rem;
}
.summary-sec article h4 {
  font-size: 0.9rem;
  font-weight: 700;
}
.summary-sec img {
  margin-bottom: -0.2rem;
  margin-right: 0.4rem;
}

button {
  font-weight: 800;
}

.attribution {
  font-size: 0.6111111111rem;
  text-align: center;
}

@media only screen and (min-width: 700px) {
  html {
    font-size: 19px;
  }
}